
import Badge from 'vux/src/components/badge/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
    props: ["item"],
    components: {
        Badge,
        dateFormat,
    },
    filters: {
        filterTime: function (value) {
            let time = value * 1000;
            return dateFormat(time, 'MM-DD');
        },
    },
    data() {
        return {
            read: this.item.read,
        }
    },
    methods: {
        //切换消息未读/已读状态
        toggleMsgRead() {
            if (this.read) {
                return ''
            }
            this.read = true
        },
    }
}




